html,body{
    height: 100%;
    overflow: hidden;
  }
  body{
    text-align: center;
  }
  body,div,h2{
    margin: 0;
    padding: 0;
  }
  body{
    font:12px/1.5 tahoma;
  }
  button{
    margin-top: 10px;
    cursor: pointer;
  }
  #overlay{
    position: absolute;
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #000;
    opacity: 0.5;
    filter: alpha(opacity=50);
    display: none;
  }
  #overlay.show{
    display: block;
  }
  #win{
    position: absolute;
    top:50%;
    left: 50%;
    width: 400px;
    height: 200px;
    background: #fff;
    border: 4px solid #f90;
    margin: -102px 0 0 -202px;
    display: none;
  }
  h2{
    font-size: 12px;
    text-align: right;
    background: #fc0;
    border-bottom: 3px solid #f90;
    padding: 5px;
  }
  h2 span{
    color: #f90;
    cursor: pointer;
    background: #fff;
    border: 1px solid #f90;
    padding:0 2px;
  }
  #win.show{
    display: block;
  }
  